import fetch from '@/utils/fetch'
/**
 * 获取今日订单应收
 * @export
 * @returns
 * @mock https://www.easy-mock.com/mock/59f1b06e4943e64bd135fdfb/witparking-op/home/parkingIncome'
 */
export function getParkingIncome () {
  return fetch({
    url: '/home/parkingIncome',
    method: 'get'
  })
}

/**
 * 获取今日充值金额
 * @export
 */
export function getValueRecharge () {
  return fetch({
    url: '/home/getValueRecharge',
    method: 'get'
  })
}

